@import '../App.scss';
.pricing__section {
    padding-top: 3%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #fafafa;
  }
  .project-img{
    width: 108%;
  }
  .theheaderp{
    text-align: center;
    margin: auto;
  }
  .p-button{
    font-family: 'Dosis', sans-serif;
    text-decoration: none;
    color:$purple;
    background-color: $white;
    padding: 2%;
    margin-top: 5%;
    margin-left: 7%;
    margin-right: 7%;
    margin-bottom: 5%;
  }
  .button-group{
    justify-content: center;
    text-align: center;
    display: flex;
    width: 100%;

  }
  .pricing__container-card {
    box-sizing: border-box;
    background: $purple;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
    width: 280px;
    height: 500px;
    text-decoration: none;
    border-radius: 4px;
    margin-top: 3%;

  }
  
  .pricing__container-card:nth-child(2) {
    transform: scale(1.05);
    background: #242424;
  }
  
  .pricing__container-card:hover {
    transform: scale(1.06);
    transition: all 0.3s ease-out;
    color: #1c2237;
  }
  
  .pricing__container-card:nth-child(2) {
    margin: 0 24px;
  }
  
  .pricing__container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .pricing__wrapper {
    display: flex;
    flex-direction: column;
    align-items: start;
    margin: 0 auto;
  }
  
  .pricing__heading {
    color: #1c2237;
    margin-bottom: 24px;
  }
  
  .pricing__container-cardInfo {
    display: flex;
    flex-direction: column;
    height: 500px;
    padding: 24px;
    align-items: center;
    color: #fff;
  }
  
  .pricing__container-cardInfo h3 {
    margin-bottom: 5px;
    font-size: 24px;
  }
  
  .pricing__container-cardInfo h4 {
    font-size: 40px;
  }
  
  .pricing__container-cardInfo p {
    font-size: 14px;
    margin-bottom: 24px;
  }
  
  .pricing__container-features {
    list-style: none;
  }
  
  .pricing__container-features li {
    margin-bottom: 10px;
  }
  
  .pricing__container-features {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .icon {
    margin: 24px 0;
  }
  .p-title{
    text-align: center;
    font-family: 'Press Start 2P', cursive;
  }
  .under-ptitle{
    font-family: 'Dosis', sans-serif;
  }
  
  @media screen and (max-width: 960px) {
    .pricing__container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
    }
  
    .pricing__container-card {
      width: 90%;
    }
  
    .pricing__wrapper {
      margin: 0 30px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .pricing__container-card:nth-child(2) {
      transform: scale(1);
      background: #242424;
      margin: 24px;
    }
  
    .pricing__container-card:hover {
      transform: none;
    }
  }